.banner{
    background-size: cover;
    height: 448px;
    color: white;
}
.content{
    padding-top:140px ;
    height: 190px;
    padding-left: 15px;
}
h1.title{
    font-weight: 800;
    font-size: 3rem;
    padding-bottom: 0.3rem;
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding: 0.5rem 2rem;
    margin-right: 1rem;
    background-color: rgb(51, 51, 51,.5);
    cursor: pointer;
}
.button:hover{
    color: black;
    background-color: #e6e6e6;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 360px;
}
.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgb(37,37,37,.61),#111);

}